import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Navigateur } from '../../../models/navigateur.model';
import { Router, ActivatedRoute } from '@angular/router';
import { NavigateurService } from '../../service/navigateur.service';


@Component({
  selector: 'app-display-navigateur',
  templateUrl: './display-navigateur.component.html',
  styleUrls: ['./display-navigateur.component.css']
})
export class DisplayNavigateurComponent implements OnInit {

  @Input() navigateur: Navigateur;
  @Input() searchTerm: String;

  @Output() notifyEdit = new EventEmitter<Number>();
  @Output() notifyDelete = new EventEmitter<Number>();

  constructor(
    private _router: Router,
    private _navigateurService: NavigateurService,
    private _route: ActivatedRoute,

  ) { }

  ngOnInit() {
  }




  viewDetailsNavigateur() {
    this._router.navigate([this.navigateur.id], { relativeTo: this._route });
  }
  editNavigateur() {
    this.notifyEdit.emit(this.navigateur.id);

  }
  deleteNavigateur() {
    this.notifyDelete.emit(this.navigateur.id);
  }

} 