import { Component, OnInit, ViewChild } from '@angular/core';


import { Observable } from 'rxjs';
import { NavigateurService } from '../../service/navigateur.service';
import { Navigateur } from '../../../models/navigateur.model';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

import { FormNavigateurComponent } from '../form-navigateur/form-navigateur.component';
import { ToastrService } from 'ngx-toastr';




@Component({
  selector: 'app-navigateurs',
  templateUrl: './navigateurs.component.html',
  styleUrls: ['./navigateurs.component.css']
})
export class NavigateursComponent implements OnInit {
  bsModalRef: BsModalRef;
  @ViewChild('NavigateurForm') public NavigateurForm: NgForm;

  public navigateur = new Navigateur();

  public navigateurs = [];
  public filteredNavigateurs = [];
  public errors = [];


  private _id: Number;
  private _searchTerm: String;



  get searchTerm(): String {
    return this._searchTerm;
  }

  set searchTerm(value: String) {
    this._searchTerm = value;
    this.filteredNavigateurs = this.filtreNavigateurs(value);
  }



  constructor(
    private _navigateurService: NavigateurService,
    private _router: Router,
    private _route: ActivatedRoute,
    private modalService: BsModalService,
    private _toastr: ToastrService


  ) { }


  createNavigateur() {
    this.openModelFormNavigateur(false);
  }

  onEditNotification(id: Number) {
    this._navigateurService.getNavigateur(id).subscribe(
      navigateurData => {
        this.navigateur = navigateurData;
        this.openModelFormNavigateur(true);

      },
      error => {
        this.errors = error;
      },
    );
  }



  openModelFormNavigateur(edit: Boolean) {

    const initialState = {
      title: edit ? 'Modifier Navigateur' : 'Nouveau Navigateur',
      navigateur: edit ? this.navigateur : new Navigateur(),
      errors: this.errors,
      edit: edit
    };

    this.bsModalRef = this.modalService.show(FormNavigateurComponent, { initialState });
    if (edit === false) {
      this.bsModalRef.content.onClose.subscribe(
        data => this._navigateurService.save(data)
          .subscribe(
            (data) => {
              this._toastr.success('<h3>Navigateur ajout avec succs</h3>', ' ', {
                'timeOut': 3000,
                'progressBar': true,
                'enableHtml': true
              });
              // renitialiser la liste des navigateurs
              this.getNavigateursList();
           // vider le navigateur
              this.navigateur = new Navigateur();
              this.errors = [];
              this.bsModalRef.content.errors = this.errors;
              this.bsModalRef.content.navigateur = this.navigateur;
              this.bsModalRef.hide();
            },
            (error) => {
              this.errors = error.error.errors;
              this.bsModalRef.content.errors = this.errors;
              this.errors = [];
            }
          )
      );


    } else {



      this.bsModalRef.content.onClose.subscribe(
        data => this._navigateurService.update(data)
          .subscribe(
            (data) => {
              this._toastr.warning('<h3>Navigateur dit avec succs</h3>', ' ', {
                'timeOut': 3000,
                'progressBar': true,
                'enableHtml': true
              });
              this.getNavigateursList();

              this.navigateur = new Navigateur();
              this.errors = [];
              this.bsModalRef.content.errors = this.errors;
              this.bsModalRef.content.navigateur = this.navigateur;
              this.bsModalRef.hide();
            },
            (error) => {
              this.errors = error.error.errors;
              this.bsModalRef.content.errors = this.errors;
              this.errors = [];
            }
          )
      );


    }






  }

  ngOnInit() {
    this.getNavigateursList();

  }



  getNavigateursList() {

    this._navigateurService.getNavigateurs()
      .subscribe(
        (navigateursApi) => {
          this.navigateurs = navigateursApi;
          if (this._route.snapshot.queryParamMap.has('searchTerm')) {
            this.searchTerm = this._route.snapshot.queryParamMap.get('searchTerm');

          } else if (this.searchTerm) {

            this.filteredNavigateurs = this.filtreNavigateurs(this.searchTerm);

          } else {
            this.filteredNavigateurs = this.navigateurs;

          };
        }, error => this.errors = error.error.errors
      );


  }








  onDeleteNavigateur(id: Number) {
    this._navigateurService.deleteNavigateur(id).subscribe(
      (data) => {

        this.getNavigateursList();
        const i = this.filteredNavigateurs.findIndex(e => e.id === id);
        if (i !== -1) {
          this.filteredNavigateurs.splice(i, 1);
          this._toastr.info('<h3>Navigateur supprim avec succs</h3>', ' ', {
            'timeOut': 3000,
            'progressBar': true,
            'enableHtml': true
          });
        }
      },
      error => console.log(error)
    );



  }



  filtreNavigateurs(searchString: String) {
    if (searchString === null) {
      this.filteredNavigateurs = this.navigateurs;
    } else {

      return this.navigateurs.filter(navigateur =>
        navigateur.nom.toLowerCase().indexOf(searchString.toLowerCase()) !== -1);
    }

  }








}
