import { Component, OnInit } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { Navigateur } from '../../../models/navigateur.model';
import { Subject } from 'rxjs/internal/Subject';

@Component({
  selector: 'app-form-navigateur',
  templateUrl: './form-navigateur.component.html',
  styleUrls: ['./form-navigateur.component.css']
})
export class FormNavigateurComponent implements OnInit {


  title: String;
  navigateur: Navigateur;
  errors: any[] = [];
  edit: Boolean;


  public onClose: Subject<Navigateur>;
  public onAnnuler: Subject<Boolean>;

  constructor(private _bsModalRef: BsModalRef) { }




  ngOnInit() {
    this.onClose = new Subject();

  }

  public onConfirm(): void {
    this.onClose.next(this.navigateur);

  }

  public onCancel(): void {
   
    this._bsModalRef.hide();
  }







}
