import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Navigateur } from '../../models/navigateur.model';
import { Observable } from 'rxjs/internal/Observable';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';



@Injectable({
  providedIn: 'root'
})
export class NavigateurService {

  private baseUrl = 'http://192.168.33.20/api/navigateurs';



  constructor(
    private _http: HttpClient
  ) { }




  getNavigateurs(): Observable<Navigateur[]> {
    return this._http.get<Navigateur[]>(this.baseUrl)
      .pipe(catchError(this.errorHandler));
  }

  errorHandler(error: HttpErrorResponse) {
    return throwError(error);
  }


  getNavigateur(id: Number): Observable<Navigateur> {
    return this._http.get<Navigateur>(this.baseUrl + '/' + id)
      .pipe(catchError(this.errorHandler));
  }


  save(navigateur: Navigateur) {
    return this._http.post<Navigateur>(this.baseUrl, navigateur)
      .pipe(catchError(this.errorHandler));
  }


  update(navigateur: Navigateur) {
    return this._http.put<Navigateur>(this.baseUrl + '/' + navigateur.id, navigateur)
      .pipe(catchError(this.errorHandler));
  }





  deleteNavigateur(id: Number): Observable<Navigateur> {
    return this._http.delete<Navigateur>(this.baseUrl + '/' + id)
      .pipe(catchError(this.errorHandler));
  }

}
