import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable } from 'rxjs/internal/Observable';
import { Region } from '../admin/region/region.model';
import { catchError } from 'rxjs/internal/operators/catchError';
import { throwError } from 'rxjs/internal/observable/throwError';


@Injectable({
  providedIn: 'root'
})
export class MapService {

  private _latitude = 34.031550;
  private _longitude = -6.031550;
  private _zoom = 1;

  getGeo() {
    this._http.get<any>('/assets/geojson/afrique.json')
      .subscribe(geojson => {
        return geojson;
        // this.afriqueLayer = geoJSON(geo);
        // this.afriqueLayer = geo.features;


      });
  }


  constructor(private _http: HttpClient) { }

  getLongitude() {
    return this._longitude;
  }
  setLongitude(longitude) {
    return this._longitude = longitude;
  }


  getLatitude() {
    return this._latitude;
  }

  setLatitude(latitude) {
    this._latitude = latitude;
  }

  getZoom() {
    return this._zoom;
  }

  setZoom(zoom) {
    this._zoom = zoom;
  }


  getJson(): Observable<any> {
    return this._http.get(' /assets/geojson/afrique.geojson')
      .pipe(catchError(this.errorHandler));
  }






  errorHandler(error: HttpErrorResponse) {
    return throwError(error);
  }
}
