import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NavigateurService } from '../../service/navigateur.service';
import { Navigateur } from '../../../models/navigateur.model';

@Component({
  selector: 'app-details-navigateur',
  templateUrl: './details-navigateur.component.html',
  styleUrls: ['./details-navigateur.component.css']
})
export class DetailsNavigateurComponent implements OnInit {
  navigateur = new Navigateur();
  private _id: Number;

  constructor(
    private _navigateurService: NavigateurService,
    private _route: ActivatedRoute,
    private _router: Router
  ) {

  }

  ngOnInit() {
    this._route.paramMap.subscribe(params => {
      this._id = +params.get('id');

      this._navigateurService.getNavigateur(this._id).subscribe(
        (data) => { this.navigateur = data; });
    });
  }



  viewSituationsNavigateur() {
    this._router.navigate(['situations'], { relativeTo: this._route });
  }


  retour() {
    this._router.navigate(['administrateur/navigateurs', { id: this._id }], { queryParamsHandling: 'preserve' });
  }

}
