import { PipeTransform, Pipe } from '@angular/core';
import { Navigateur } from './../../../models/navigateur.model';

@Pipe({
    name: 'navigateurFilter',
    pure: true
})
export class NavigateurFilterPipe implements PipeTransform {

    transform(navigateurs: Navigateur[], searchTerm: string): Navigateur[] {
        if (!navigateurs || !searchTerm) {
            return navigateurs;
        }

        return navigateurs.filter(navigateur =>
            navigateur.nom.toLowerCase().indexOf(searchTerm.toLowerCase()) !== -1);


    }
}