import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { NavigateurService } from './navigateur.service';
import { Injectable } from '@angular/core';

@Injectable()
export class NavigateurDetailsGuardService implements CanActivate {

    constructor(private _navigateurService: NavigateurService, private _router: Router) {

    }
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        const navigateurExists = !!this._navigateurService.getNavigateur(+route.paramMap.get('id'));

        if (navigateurExists) {
            return true;
        } else {
            // tslint:disable-next-line:no-unused-expression
            this._router.navigateByUrl('/navigateurNotFound');
            return false;
        }


    }
}