import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './admin.component';
import { Navigateur } from '../models/navigateur.model';
import { NavigateursComponent } from './navigateur/navigateurs/navigateurs.component';
import { DetailsNavigateurComponent } from './navigateur/details-navigateur/details-navigateur.component';

import { NavigateurNotFoundComponent } from './navigateur/navigateurs/navigateur-not-found/navigateur-not-found.component';
import { NavigateurDetailsGuardService } from './service/navigateur-details-guard.service';
import { AfterLoginService } from '../services/after-login.service';
import { SituationsComponent } from './navigateur/situation/situations/situations.component';
import { RegionComponent } from './region/region.component';
import { DetailsRegionComponent } from './region/details-region/details-region.component';
import { CountryComponent } from './country/country.component';


const routes: Routes = [
  {
    path: 'administrateur', component: AdminComponent, canActivate: [AfterLoginService],
    children: [
      { path: 'navigateurs', component: NavigateursComponent },
      {
        path: 'navigateurs/:id', component: DetailsNavigateurComponent, canActivate: [NavigateurDetailsGuardService],
        children: [
          { path: 'situations', component: SituationsComponent }
        ]
      },
      { path: 'regions', component: RegionComponent },
      { path: 'regions/:id', component: DetailsRegionComponent },
      {path: 'countries', component: CountryComponent}
    ]

  },
  { path: 'navigateurNotFound', component: NavigateurNotFoundComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule {CountryComponent }
